import React, { useState } from "react";
import { ReactSVG } from "react-svg";
import cn from "classnames";
import { ReactComponent as Hamburger } from "../img/icons/hamburger.svg";
import close from "../img/icons/close.svg";
import telegram from "../img/icons/telegram.svg";
import facebook from "../img/icons/facebook.svg";
import insta from "../img/icons/insta.svg";

const SideBar = (props) => {
    const [toggle, setToggle] = useState(false);
    const [language, setLanguage] = useState('Eng')

    const toggleMenu = () => {
        setToggle(!toggle);
    }

    const changeLanguage = () => {
        language === 'Eng' ? setLanguage('Рус') : setLanguage('Eng');
    }

    return (
        <div>
            <div className={props.sidebar.menu}>
                <div className={cn(props.sidebar.menu__icon, props.sidebar.open)} onClick={toggleMenu}>
                    <Hamburger />
                </div>
                <div className={props.sidebar.menu__language} onClick={changeLanguage}>{ language }</div>
            </div>
            <div className={cn(props.sidebar.wrapp, {[props.sidebar.opened]:toggle})}>
                <div className={props.sidebar.menu__opened}>
                    <div className={props.sidebar.menu__icon} onClick={toggleMenu}>
                        <ReactSVG src={close}/>
                    </div>
                    <nav className={props.sidebar.menu__list}>
                        <a className={props.sidebar.link} href="#">ПАРКОВКА</a>
                        <a className={props.sidebar.link} href="#">СТРАХОВКА</a>
                        <a className={props.sidebar.link} href="#">БЕНЗИН</a>
                        <a className={props.sidebar.link} href="#">ОБСЛУЖИВАНИЕ</a>
                    </nav>
                    <div className={props.sidebar.menu__social}>
                        <div className={props.sidebar.social__icon}>
                            <ReactSVG src={telegram}/>
                        </div>
                        <div className={props.sidebar.social__icon}>
                            <ReactSVG src={facebook}/>
                        </div>
                        <div className={props.sidebar.social__icon}>
                            <ReactSVG src={insta}/>
                        </div>
                        <div className={cn(props.sidebar.menu__language, props.sidebar.mobile)} onClick={changeLanguage}>{ language }</div>
                    </div>
                </div>
                <div className={props.sidebar.slider__back}></div>
            </div>
        </div>
    )
}

export default SideBar;